.main-container {
  // max-width: $normal-breakpoint;
  margin: 0 auto;
  padding: 0;

  // @include for-wider-layout {
  //   min-width: $wider-breakpoint;
  //   margin: 0 95px;
  // }

  @include for-wide-layout {
    width: $normal-breakpoint;
  }
  @include for-tablet-layout {
    max-width: $tablet-breakpoint;
    padding: 0 40px;
  }
  @include for-mobile-layout {
    max-width: $mobile-breakpoint;
    padding: 0 24px;
    margin: unset;
  }
}
